<template>
  <div class="language-selector">
    <div class="selector" @click="toggleSelector" v-bind:class="{ opened: selectorOpened }">
      <ul class="language-list text-white">
        <li v-for="lang in languages" :key="lang.lang" @click="selectLanguage(lang.lang)">
                <span class="language-flag">
                    <img v-bind:src="lang.flag" alt=""/>
                </span>
          <span class="language-text">{{ lang.name }}</span>
          <span class="arrow" v-if="lang.order === 0"></span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
// import { mapState } from 'vuex';

export default {
  data() {
    return {
      lang: this.$store.state.lang,
      selectorOpened: false,
      selectedLanguage: `pl`,
      languages: [
        {
          lang: `pl`,
          flag: require(`./../../assets/svg/flag-pl.svg`),
          name: `Polski`,
          order: 0
        },
        {
          lang: `en`,
          flag: require(`./../../assets/svg/flag-en.svg`),
          name: `English`,
          order: 1
        },
        {
          lang: `it`,
          flag: require(`./../../assets/flag-it.png`),
          name: `Italiano`,
          order: 2
        }
      ]
    }
  },
  created() {
    this.updateOrder();
  },
  methods: {
    updateOrder() {
      let order = 1;
      this.languages = this.languages.map(lang => {
        if (lang.lang === this.lang) {
          lang.order = 0;
        } else {
          lang.order = order;
          order++;
        }

        return lang;
      });
      this.languages = this.languages.sort((a, b) => a.order > b.order ? 1 : -1);
    },
    toggleSelector() {
      this.selectorOpened = !this.selectorOpened;
    },
    selectLanguage(language) {
      if (language !== this.lang) {
        this.lang = language;
        this.updateOrder();
        this.$store.commit('setLanguage', language);
      }
    },
  },
  watch: {
    '$store.state.lang': function (lang) {
      this.lang = lang;
    },
  },
}
</script>

<style>
.language-selector {
  margin: 20px 0 0;
  position: relative;
  width: 220px;
  height: 50px;
}

.sidebar .language-selector .selector {
  width: 100%;
}

.language-selector .selector {
  box-sizing: border-box;
  position: relative;
  overflow: hidden;
  border: 1px solid #fff;
  width: 100%;
  cursor: pointer;
  max-height: 50px;
  border-radius: 2px;
  transition: max-height 0.5s cubic-bezier(0.78, 0, 0.235, 1) 0s;
}

.language-selector .selector.opened {
  max-height: 200px;
}

.language-selector .language-list {
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  z-index: 1;
}

.language-selector .language-list li {
  display: flex;
  height: 50px;
  justify-content: center;
  align-items: center;
  position: relative;
  background: #1b1b1b;
}

.language-selector .language-flag {
  display: block;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 10px;
  transform: translate(0, -50%);
}

.language-selector .language-flag img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}

.arrow {
  width: 0.5rem;
  height: 0.5rem;
  border-top: 1.5px solid rgb(245, 241, 236);
  border-right: 1.5px solid rgb(245, 241, 236);
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translate3d(0px, -70%, 0px) rotate(135deg);
  transition: all 0.5s ease 0s;
}

.language-selector .selector.opened .arrow {
  transform: translate3d(0px, -50%, 0px) rotate(-45deg);
}
</style>